import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { PagePermissionBean } from '../bean/page-permission.bean'
import { CommonHttpService } from '../../../../http/common.http.service';
import { Message } from 'primeng/primeng';
// import { OperatingLevel } from '../../../../constant/common.codeValue';
import {  tableMessage } from 'app/@uisftech/common/constant/config';


@Component({
  selector: 'app-page-permission-list',
  templateUrl: './page-permission-list.component.html'
})
export class PagePermissionListComponent implements OnInit {
  @Input() inValue: string;

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  pagePermissionBean: PagePermissionBean = new PagePermissionBean();
  // 表格中显示的信息
  tabMesg: any = tableMessage;
  //提示信息
  msgs: Message[] = [];
  //模态框左上角标题
  title: string = '权限列表'
  //接收后台返回的表头数据
  tableData: any;
  //接收后台返回的permissionId的数组
  tableValueData: any;
  //将permissionId取出放入该list种
  valueList: any;
  //  operatingLevel1:any = OperatingLevel

  roleTypeId: string;
  operatingLevel: string;
  operatingLevelName:string;
  roleId: string;
  selectedCities: any;
  pagePermission: any;
  total: string;
  roleTypeName:string;
  roleName:string;
  code:any;
  constructor(
    public commonHttpService: CommonHttpService
  ) {
    // this.codeValues() //调用方法，获取全部码值
// this.tranType = this.code['tranType']; 
   }

  
  ngOnInit() {
    this.pagePermission = this.inValue;
    this.roleTypeId = this.pagePermission.roleTypeId
    this.roleTypeName = this.pagePermission.roleTypeName
    this.roleId = this.pagePermission.roleId
    this.roleName = this.pagePermission.roleName
    this.operatingLevel = this.pagePermission.operatingLevel
    if(this.pagePermission.operatingLevel=='GROUP'){
      this.operatingLevelName='集团'
    }else if(this.pagePermission.operatingLevel=='GENERAL_BANK'){
      this.operatingLevelName='总行'
    }else if(this.pagePermission.operatingLevel=='GENERAL_BANK_DT'){
      this.operatingLevelName='总行管理部门'
    }else if(this.pagePermission.operatingLevel=='BRANCH'){
      this.operatingLevelName='分行/区域'
    }else if(this.pagePermission.operatingLevel=='BRANCH_DT'){
      this.operatingLevelName='分行/区域管理部门'
    }else if(this.pagePermission.operatingLevel=='DISTRICT'){
      this.operatingLevelName='片区'
    }else if(this.pagePermission.operatingLevel=='DISTRICT_DT'){
      this.operatingLevelName='片区部门'
    }else if(this.pagePermission.operatingLevel=='SUB_BRANCH'){
      this.operatingLevelName='支行'
    }else if(this.pagePermission.operatingLevel=='SUB_BRANCH_DT'){
      this.operatingLevelName='支行部门'
    }else if(this.pagePermission.operatingLevel=='TEAM'){
      this.operatingLevelName='团队'
    }
    this.query();
    this.queryValue();
    
  }
  //查询权限列表 调角色列表查询 查出所有权限
  query() {
    let param = {
      systemFlag: '1',
      pageSize: 1000000,
      pageNum: 1,
      permissionType:'DATASOURCE'
    }
    this.commonHttpService.querySecurityPermissionList(param).subscribe(data => {
      this.tableData = data.permissionList
    })
    this.pagePermissionBean = new PagePermissionBean()
  }
  //查询已有的页面权限
  queryValue() {
    this.pagePermission = this.inValue;
    let param = {
      roleTypeId: this.pagePermission.roleTypeId,
      operatingLevel: this.pagePermission.operatingLevel,
      roleId: this.pagePermission.roleId,
      permissionId: '0'
    }
    this.commonHttpService.queryRolePostOrglevelRel(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.selectedCities = [];
        if (data.relList) {
          data.relList.forEach(element => {
            this.selectedCities.push(element.permissionId);
          });
        }
      }
    })
  }
  //确定按钮
  preserveClick() {
    let param = {
      operatingLevel: this.pagePermission.operatingLevel,
      roleTypeId: this.pagePermission.roleTypeId,
      roleId: this.pagePermission.roleId,
      permissionIdList: this.selectedCities,
      flag:'CREATE'
    }
    this.commonHttpService.createRolePostOrglevelRel(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        // this.pagePermissionBean = new PagePermissionBean();
        this.outValue.emit(false);
        this.pagePermissionBean.pageNum = 1;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }

  //取消
  cancel() {
    this.outValue.emit(false);
  }
}
