import { Component, OnInit } from '@angular/core';
// import { OrderHttpService } from './../../../http/order.http.service';
import { PagePermissionBean } from './bean/page-permission.bean';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CommonHttpService } from '../../../http/common.http.service';
// import { OperatingLevel, OrgTypes, PostName } from '../../../constant/common.codeValue';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';


//页面权限管理
@Component({
  selector: 'app-page-permission-manage',
  templateUrl: './page-permission-manage.component.html',
  styleUrls: ['./page-permission-manage.component.scss'],
  providers: [Commfunc, ConfirmationService]
})
export class PagePermissionManage implements OnInit {
  // 表格中显示的信息
  tabMesg: any = tableMessage;
  //codeValue
  //机构类型
  orgType: any = [];
  //机构级别
  operatingLevel: any = [];
  //岗位名称
  //  postName:any = PostName
  //表数据
  tableData: any = [];
  //bean
  pagePermissionBean: PagePermissionBean = new PagePermissionBean();
  //模态框的标题
  headerTitle: string;
  //模态框是否显示
  display: boolean = false;
  showModel: string;
  //修改的参数
  inValue: any = [];
  //总页数
  total: string;
  //每页多少条
  pageSize: number = 10;
  //提示信息
  msgs: Message[] = [];
  //页面头部显示信息
  precredit: string = '页面权限管理';
  list: any = [];//业务角色模糊查询下拉框
  list1: any = [];//岗位角色模糊查询的下拉框
  rows: any;
  currentPage: any;
  first: number = 0;
  code: any;
  constructor(
    public commonHttpService: CommonHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.orgType = this.code['OrgTypes'];
    this.operatingLevel = this.code['OperatingLevel'];
  }

  ngOnInit() {
    this.pagePermissionBean.pageSize = 10;
    this.pagePermissionBean.pageNum = 1;
    this.queryClick();
    this.queryRole();
    this.queryPost()

    //按回车键查询
    let _thiss = this;
    document.onkeydown = function (event) {
        if (event && event.keyCode === 13) {
            _thiss.query();
        }
    }
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //业务角色模糊查询
  queryRole() {
    let param = {
      pageSize: 1000000
    }
    this.commonHttpService.querySpBusinessRole(param).subscribe(data => {
      if (data.businessRoleList) {
        this.list = this.commfunc.listToLabelvalue(data.businessRoleList, 'description', 'roleTypeId');
      }
    })

  }
  //角色名称模糊查询
  queryPost() {
    let param = {
      pageSize: 1000000
    }

    this.commonHttpService.queryRoleListInfo(param).subscribe(data => {
      if (data.roleListInfo) {
        this.list1 = this.commfunc.listToLabelvalue(data.roleListInfo, 'roleName', 'roleId');
      }
    })
  }

  //查询
  queryClick() {
    this.commonHttpService.queryRolePostOrglevelRel(this.pagePermissionBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.relList;
        this.total = data.total;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  query() {
    this.first = 0;
    this.pagePermissionBean.pageNum = 1;
    this.commonHttpService.queryRolePostOrglevelRel(this.pagePermissionBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.relList;
        this.total = data.total;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }


  //删除按钮事件
  deleteRole(car) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = {
          roleTypeId: car.roleTypeId,
          operatingLevel: car.operatingLevel,
          roleId: car.roleId,
          flag: '',
        };
        this.commonHttpService.createRolePostOrglevelRel(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.pagePermissionBean.pageNum = 1;
            this.queryClick();
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
        })
      }
    });
  }
  //新增按钮事件
  addClick() {
    this.headerTitle = '新增页面权限';
    this.display = true;
    this.showModel = '1';
  }
  //新增的回调
  addCall(param) {
    this.display = param;
    this.pagePermissionBean.pageNum = 1;
    // this.resetClick();
    this.queryClick();
  }
  //权限列表查询
  outCall(param) {
    this.display = param;
    this.queryClick();
  }
  //权限列表查询按钮
  powerList(car) {
    this.headerTitle = '分配权限';
    this.display = true;
    this.showModel = '2';
    this.inValue = car;

  }

  //分页
  paginate(event) {
    this.rows = event.rows;
    //每页显示的条数
    this.pagePermissionBean.pageSize = this.rows;
    //当前页
    this.currentPage = event.page + 1;
    this.pagePermissionBean.pageNum = this.currentPage;
    this.first = event.rows * event.page;
    //调用查询的方法
    this.queryClick();
    // this.pagePermissionBean.pageNum = 1;
  }

  //重置
  reset() {
    this.pagePermissionBean = new PagePermissionBean();
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}


