import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { PagePermissionBean } from '../bean/page-permission.bean'
import { CommonHttpService } from '../../../../http/common.http.service';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Message } from 'primeng/primeng';
// import { OperatingLevel } from '../../../../constant/common.codeValue'
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
@Component({
  selector: 'app-page-permission-add',
  templateUrl: './page-permission-add.component.html',
  styleUrls: ['./page-permission-add.component.scss'],
  providers: [Commfunc]
})
export class PagePermissionAddComponent implements OnInit {
  @Input() inValue: string;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  tabMesg: any = tableMessage;// 表格无数据时显示的信息
  pagePermissionBean: PagePermissionBean = new PagePermissionBean();
  userform: FormGroup; //校验
  //提示信息
  msgs: Message[] = [];
  title2: string = '权限';
  //机构级别
  operatingLevel: any = [];
  list2: any;//接收后台返回的权限组
  list: any = [];//业务角色模糊查询下拉框
  list1: any = [];//角色名称模糊查询的下拉框
  checkValueList: Array<any> = [];//复选框的值
  tableData: any;//接收后台返回的列表
  selectedCities: any = [];//取勾选框的值
  code: any;
  constructor(
    public commonHttpService: CommonHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc
  ) {
    this.codeValues() //调用方法，获取全部码值
    this.operatingLevel = this.code['OperatingLevel'];
  }

  ngOnInit() {
    this.userform = this.fb.group({
      'roleTypeId': new FormControl('', Validators.required),
      'operatingLevel': new FormControl('', Validators.required),
      'roleId': new FormControl('', Validators.required)
    });
    this.query()
    this.queryPost()
    this.queryRole()
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  ngOnChanges() {
    this.ngOnInit()
  }
  //业务角色模糊查询
  queryRole() {
    this.pagePermissionBean.pageSize = 1000000
    this.commonHttpService.querySpBusinessRole(this.pagePermissionBean).subscribe(data => {
      if (data.businessRoleList) {
        this.list = this.commfunc.listToLabelvalue(data.businessRoleList, 'description', 'roleTypeId');
      }
    })
  }
  //角色名称模糊查询
  queryPost() {
    this.pagePermissionBean.pageSize = 1000000
    // this.commonHttpService.queryVaguePostList(this.pagePermissionBean).subscribe(data => {
    //   if (data.postList) {
    //     this.list1 = this.commfunc.listToLabelvalue(data.postList, 'postName', 'postId');
    //   }
    // })
    this.commonHttpService.queryRoleListInfo(this.pagePermissionBean).subscribe(data => {
      if (data.roleListInfo) {
        this.list1 = this.commfunc.listToLabelvalue(data.roleListInfo, 'roleName', 'roleId');
      }
    })
  }
  //查询权限组调的是角色列表查询
  query() {
    this.pagePermissionBean.systemFlag = '1';
    this.pagePermissionBean.pageSize = 1000000;
    this.pagePermissionBean.pageNum = 1;
    this.pagePermissionBean.permissionType = 'DATASOURCE';
    this.commonHttpService.querySecurityPermissionList(this.pagePermissionBean).subscribe(data => {
      this.tableData = data.permissionList;
    })
  }


  // 确定
  preserveClick() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    //校验权限有无勾选
    if (this.selectedCities.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: "error", summary: "提示", detail: "请选择权限" })
      return;
    }
    let listCustNo = []
    let arr = []
    this.selectedCities.forEach((ri) => {
      let obj = {};
      obj['groupId'] = this.tableData[ri].permissionsId
      listCustNo.push(obj)
    });
    listCustNo.forEach((item) => {
      arr.push(item["groupId"])
    })

    let param = {
      operatingLevel: this.pagePermissionBean.operatingLevel,
      roleTypeId: this.pagePermissionBean.roleTypeId,
      roleId: this.pagePermissionBean.roleId,
      permissionIdList: arr,
      flag: 'CREATE'
    }
    this.commonHttpService.createRolePostOrglevelRel(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.pagePermissionBean = new PagePermissionBean();
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  //取消
  cancel() {
    this.outValue.emit(false);
  }
}
